const addTipsToHealthTable = (chakraList, lng) => {
  const healthCardTips = getHealthChakraTips(lng);
  chakraList.forEach(element => {
    const tip = healthCardTips.find(tipEl => tipEl.chakraName === element.chakraName);
    element.tip = tip;
  });
};

